import React from 'react';
import './App.scss';

import cts from './contents.json';  // list of items

const MasonryHeader = props =>
  <div className="masonry-header">{props.title}
    <span className="masonry-header-sub">{props.sub}</span>
  </div>;

const MasonryItem = props => {
  const getContentURL = cnt => (cts.URLs.root + cts.URLs.cont   + cnt);
  const getImageURL   = img => (cts.URLs.root + cts.URLs.images + img + cts.URLs.imgExt);
  const getBkColor    = d => {
    if (diffDays <= 30) {
      return `rgba(231, 76, 60, ${.4+.6*(30-diffDays)/30})`
    } else if (diffDays <= 366/2) {
      return `rgba(0, 100, 0, .6)`
    } else if (diffDays <= 366) {
      return `rgba(0, 0, 100, .6)`
    } else {
      return `rgba(0, 0, 0, .6)`
    }
  };
  const mon_name = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
  const dt = new Date(props.date);
  const diffDays = Math.ceil(( Date.now() - dt.getTime() ) / (1000 * 60 * 60 * 24));
  let bgc = getBkColor(diffDays);

  return (
    <div className="masonry-item">
      <div className="masonry-content">
        <div className="masonry-img">
          <a href={getContentURL(props.content)} target="_blank" rel="noopener noreferrer">
            <div className="date-stamp" style={{backgroundColor: bgc}}>
              <div className="date-mon-dd">{mon_name[dt.getMonth()] + '.' + dt.getDate()}</div>
              <div className="date-year">{dt.getFullYear()}</div>
            </div>
            <img src={getImageURL(props.content)} alt={props.alt} />
          </a>
        </div>
        <div className="masonry-text">
          <h3 className="masonry-title">{props.title}
            {props.nb && <span className="masonry-nb">{" " + props.nb}</span>}
          </h3>
          <p className="masonry-description">{props.desc}</p>
        </div>
      </div>
    </div>
  );
}

const MasonryFooter = props =>
  <div className="masonry-footer">
    <p>
      <a href={props.url} target="_blank" rel="external noopener noreferrer nofollow">{props.title}</a>
      {" " + props.copyright}
      <a href={props.mailto}>{" " + props.contact}</a>
    </p>
  </div>;

// ES2018 property spread notation {...props}
const App = () => {
  let lns = [];
  let id=0;
  cts.Items.forEach(item =>
    lns.push( <MasonryItem key = {id++} {...item} /> )
  );
  //console.log(id, "items");

  return (
    <div className="App">
      <MasonryHeader {...cts.Header} />
      <div className="masonry-wrapper">
        <div className="masonry">
          {lns}
        </div>
      </div>
      <MasonryFooter {...cts.Footer} />
    </div>
  );
}

export default App;
